<template>
  <div class="product-items" v-if="notCategoryProducts.length">
    <div v-for="(item, index) in notCategoryProducts" :key="index">
      <router-link :to="`/edit/${item.doc._id}`" class="d-block card product-items__item text-decoration-none mb-2">
        <div class="card-body d-flex">
          <div class="product-items__item-avatar mr-2">
            <img class="mx-auto" v-if="item.doc.image" :src="item.doc.image"/>
          </div>
          <div class="product-items__item-content w-100 d-flex flex-column">
            <h6>{{item.doc.title}}</h6>
            <div class="mt-auto">
              <p class="text-center text-primary m-0">На <strong>{{ item.doc.gram || '100' }}</strong> гр. продукту</p>
              <div class="item-information d-flex text-center">
                <div class="item-information__item flex-fill d-flex flex-column">
                  <span class="item-information__header">БЖ</span>
                  <span class="item-information__footer">{{ bxbjCalc(bxCalc(item.doc.product_b, item.doc.gram), bjCalc(item.doc.product_j, item.doc.gram))}}</span>
                </div>
                <div class="item-information__item flex-fill d-flex flex-column">
                  <span class="item-information__header">У</span>
                  <span class="item-information__footer">{{ byCalc(item.doc.product_y, item.doc.gram)}}</span>
                </div>
                <div class="item-information__item flex-fill d-flex flex-column">
                  <span class="item-information__header">ХЕ</span>
                  <span class="item-information__footer">{{ allCalc(bxCalc(item.doc.product_b, item.doc.gram),bjCalc(item.doc.product_j, item.doc.gram),byCalc(item.doc.product_y, item.doc.gram))}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CategoryNotPage',
  data() {
    return {
      products: [],
      categories: [],
      notCategoryProducts: [],
    }
  },
  computed: {
    url() {
      return this.$route.params.id
    },
    bx_param: {
      get() {
        return this.$store.state.option.bx_param
      }
    },
    bj_param: {
      get() {
        return this.$store.state.option.bj_param
      }
    },
    by_param: {
      get() {
        return this.$store.state.option.by_param
      }
    }
  },
  methods: {
    calculate(value, gram) {
      return (parseFloat(value) * gram) / 100
    },
    bxCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bx_param)) / parseInt(this.by_param)).toFixed(1);
    },
    bjCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bj_param)) / parseInt(this.by_param)).toFixed(1);
    },
    byCalc(value, gram) {
      return (this.calculate(value, gram) / parseInt(this.by_param)).toFixed(1);
    },
    bxbjCalc(p1, p2) {
      return (parseFloat(p1) + parseFloat(p2)).toFixed(1);
    },
    allCalc(p1, p2, p3) {
      return (parseFloat(p1) + parseFloat(p2) + parseFloat(p3)).toFixed(1);
    },
    calculateCategoryAndProducts() {
      let fondP = null;
      this.products.forEach((val) => {
        fondP = this.categories.find((x) => {
          return x.id === val.doc.category_id
        });
        if (!fondP) {
          this.notCategoryProducts.push(val)
        }
        fondP = null;
      })
    },
    async getProducts() {
      try {
        await this.$pouch.allDocs({
          include_docs: true,
          attachments: true
        }).then((res) => {
          this.products = res.rows;
          this.calculateCategoryAndProducts();
        });
      } catch (error) {
        console.error(error)
      }
    },
    async getCategories() {
      try {
        await this.$pouch.allDocs({
          include_docs: true,
          attachments: true
        }, 'category').then((res) => {
          this.categories = res.rows
        });
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted() {
    console.log('test');
    this.getCategories().then(()=> {
      this.getProducts();
    })
  }
}
</script>

<style scoped lang="scss">
.product-items {
  .card-body {
    padding: 5px;
  }
  &__item {
    color: #000000;
    &-avatar {
      width: 100px;
      min-width: 100px;
    }
    &-content{

    }
  }
  .item-information {
    &__item {
      margin: 0 2px;
      .item-information__header {
        border-bottom: 1px solid green;

      }
      .item-information__footer {
        font-weight: 500;
        font-size: 18px;
      }
    }
  }
}
</style>
